.myModal {
  z-index: 5;
  position: fixed;
  top: 0;
  left: 0;
  display: none;
  height: 100%;
  width: 100%;
  background: rgba(0, 0, 0, 0.637);
  margin: 0 auto;
}
.myModalContent {
  padding: 25px;
  background: rgba(78, 92, 119, 0.907);
  border-radius: 5px;
}

.myModal.active {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.lds-dual-ring {
  display: inline-block;
  width: 80px;
  height: 80px;
}
.lds-dual-ring:after {
  content: " ";
  display: block;
  width: 64px;
  height: 64px;
  margin: 8px;
  border-radius: 50%;
  border: 6px solid #fff;
  border-color: #fff transparent #fff transparent;
  animation: lds-dual-ring 1.2s linear infinite;
}
@keyframes lds-dual-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.message{
  border-bottom: 1px solid black;
}
.messageInside{
  display: grid;
  /* grid-template-columns: 40px 1fr; */
  margin-bottom: 10px;
}
.messageInside img{
  width: 40px;
  border-radius:50%;
}
.messageInside p{
  font-size: 18px;
